"use client";
import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

export default function LoginPage() {
  const [showModal, setShowModal] = useState(false);
  const [formData, setFormData] = useState({ firstName: "", zipCode: "" });

  const handleFinish = () => {
    if (formData.firstName && formData.zipCode.length === 5) {
      // Save simulated session
      localStorage.setItem("user_profile", JSON.stringify(formData));
      window.location.href = "/feed"; // Move to the community feed
    }
  };

  return (
    <div className="relative flex flex-col items-center justify-center min-h-screen bg-[#FFFDF0] p-6 font-sans overflow-hidden">
      {/* 1. MAIN LOGIN CARD */}
      <motion.div 
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        className="w-full max-w-sm bg-white rounded-[2.5rem] shadow-[0_25px_0_#E5E5E5] p-10 border-2 border-[#E5E5E5] text-center"
      >
        <div className="w-24 h-24 bg-[#FFD900] rounded-full mx-auto mb-6 flex items-center justify-center">
          <span className="text-5xl">üôå</span>
        </div>

        <h1 className="text-3xl font-black text-slate-800 tracking-tighter uppercase mb-2">DeedDash</h1>
        <p className="text-slate-500 font-bold text-sm mb-10 leading-tight">
          Help your neighborhood. <br /> Sign in to join your community.
        </p>

        <button
          onClick={() => setShowModal(true)}
          className="w-full flex items-center justify-center gap-3 bg-white border-2 border-[#E5E5E5] p-4 rounded-2xl font-black text-slate-700 transition-all active:translate-y-1 active:border-b-0 border-b-4 hover:bg-slate-50"
        >
          <img src="https://www.gstatic.com/firebasejs/ui/2.0.0/images/auth/google.svg" alt="Google" className="w-5 h-5" />
          Continue with Google
        </button>
      </motion.div>

      {/* 2. THE SIMULATED AUTH MODAL */}
      <AnimatePresence>
        {showModal && (
          <>
            {/* Backdrop */}
            <motion.div 
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="absolute inset-0 bg-slate-900/40 backdrop-blur-sm z-40"
            />

            {/* Modal */}
            <motion.div 
              initial={{ opacity: 0, scale: 0.9, y: 20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.9, y: 20 }}
              className="absolute z-50 w-[90%] max-w-md bg-white rounded-[2rem] shadow-2xl p-8 border-b-8 border-slate-200"
            >
              <div className="text-center space-y-4">
                <div className="text-4xl">üìç</div>
                <h2 className="text-2xl font-black text-slate-800 uppercase italic">Almost there!</h2>
                <p className="text-slate-500 font-bold text-sm leading-tight">
                  We just need a few details to find your local community.
                </p>

                <div className="space-y-4 pt-4">
                  <div className="text-left">
                    <label className="text-[10px] font-black text-slate-400 uppercase ml-2">First Name</label>
                    <input 
                      type="text" 
                      placeholder="Alex"
                      className="w-full p-4 border-2 border-slate-100 rounded-2xl outline-none focus:border-blue-500 font-bold"
                      onChange={(e) => setFormData({...formData, firstName: e.target.value})}
                    />
                  </div>

                  <div className="text-left">
                    <label className="text-[10px] font-black text-slate-400 uppercase ml-2">Zip Code</label>
                    <input 
                      type="text" 
                      maxLength={5}
                      placeholder="90210"
                      className="w-full p-4 border-2 border-slate-100 rounded-2xl outline-none focus:border-blue-500 font-bold"
                      onChange={(e) => {
                        const val = e.target.value.replace(/[^0-9]/g, '');
                        setFormData({...formData, zipCode: val});
                      }}
                    />
                  </div>

                  <button 
                    disabled={!formData.firstName || formData.zipCode.length < 5}
                    onClick={handleFinish}
                    className="w-full bg-[#58CC02] disabled:bg-slate-200 text-white p-5 rounded-2xl font-black shadow-[0_4px_0_rgb(60,135,0)] active:shadow-none active:translate-y-1 uppercase transition-all mt-4"
                  >
                    Join My Community
                  </button>
                </div>
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </div>
  );
}